<template>
  <div>
    <b-form @submit.prevent="submit" class="padding-level-1">
      <b-row>
        <b-col cols="12" xl="2">
          <label class="highLightHeaderLabel">{{ $t("ACCOUNT") }}</label>
        </b-col>
        <b-col cols="12" xl="3">
          <b-form-input
            v-model="account"
            class="form-control"
            disabled
          ></b-form-input>
        </b-col>
      </b-row>
      <b-row>
        <b-col cols="12" xl="2">
          <label class="highLightHeaderLabel">{{ $t("ITEMCODE") }}</label>
        </b-col>
        <b-col cols="12" xl="3">
          <b-form-input
            v-model="itemCode"
            class="form-control"
            value="111"
            disabled
          ></b-form-input>
        </b-col>
        <b-col cols="12" xl="2">
          <label class="highLightHeaderLabel">{{ $t("ITEMNAME") }}</label>
        </b-col>
        <b-col cols="12" xl="3">
          <b-form-input
            v-model="itemName"
            class="form-control"
            value="ガス代"
            disabled
          ></b-form-input>
        </b-col>
      </b-row>
      <br />
      <b-row>
        <b-col xl="4">
          <b-row class="d-flex align-items-center">
            <b-col cols="12" xl="6">
              <b-button
                variant="primary"
                class="px-2 required-btnAny align-content-center"
                >{{ $t("REQUIREDBUTTON") }}</b-button
              >
              &nbsp;&nbsp;
              <label class="highLightHeaderLabel">{{
                $t("SETTINGYEAR")
              }}</label>
            </b-col>
            <b-col cols="12" xl="6">
              <b-form-select
                v-model="selectedYear"
                :plain="true"
                :options="options"
                value-field="item"
                text-field="name"
              ></b-form-select>
            </b-col>
          </b-row>
        </b-col>
        <b-col xl="4">
          <b-row class="d-flex align-items-center">
            <b-col cols="12" xl="6">
              <b-button
                variant="primary"
                class="px-2 required-btnAny align-content-center"
                >{{ $t("REQUIREDBUTTON") }}</b-button
              >
              &nbsp;&nbsp;
              <label class="highLightHeaderLabel">{{
                $t("COMPANYSIZE")
              }}</label>
            </b-col>
            <b-col cols="12" xl="6">
              <b-form-select
                v-model="$v.selectedCompanySize.$model"
                :plain="true"
                :options="CompanySizeoptions"
                value-field="item"
                text-field="name"
              ></b-form-select>
              <show-errors
                :control="$v.selectedCompanySize"
                :field="$t('COMPANYSIZE')"
              />
            </b-col>
          </b-row>
        </b-col>
      </b-row>
      <br />
      <b-row class="mb-2">
          <b-col xl="4">
            <b-row class="d-flex align-items-center">
              <b-col cols="12" xl="6">
                <b-button
                variant="primary"
                class="px-2 required-btnAny align-content-center"
                >{{ $t("REQUIREDBUTTON") }}</b-button
                >
                &nbsp;&nbsp;
                <label class="highLightHeaderLabel">{{
                  $t("STANDARDMONTHLYFEE")
                }}</label>
              </b-col>
              <b-col>
                <b-row class="d-flex align-items-center">
                  <b-col cols="10" xl="8">
                    <b-form-input
                      v-model="monthlyFee"
                      maxlength="7"
                      onkeypress="return ( (event.charCode !=8 && event.charCode ==0) || (event.charCode >= 48 && event.charCode <= 57))"
                      class="form-control"
                      value="50"
                    ></b-form-input>
                  </b-col>
                  <b-col cols="2" xl="1">
                    {{ $t("DASHBOARD.COST_UNIT") }}
                  </b-col>
                </b-row>
              </b-col>
            </b-row>
          </b-col>
          <b-col xl="4">
            <b-row class="d-flex align-items-center">
              <b-col cols="12" xl="6">
                <b-button
                variant="primary"
                class="px-2 required-btnAny align-content-center"
                >{{ $t("REQUIREDBUTTON") }}</b-button
                >
                &nbsp;&nbsp;
                <label class="highLightHeaderLabel">{{
                   $t("INDUSTRYAVERAGE")
                }}</label>
              </b-col>
              <b-col>
                <b-row class="d-flex align-items-center">
                  <b-col cols="10" xl="8">
                    <b-form-input
                      v-model="costFeeUpperLimit"
                      maxlength="7"
                      onkeypress="return ( (event.charCode !=8 && event.charCode ==0) || (event.charCode >= 48 && event.charCode <= 57))"
                      class="form-control"
                      value="150,000"
                    ></b-form-input>
                  </b-col>
                  <b-col cols="2" xl="1">
                    {{ $t("DASHBOARD.COST_UNIT") }}
                  </b-col>
                </b-row>
              </b-col>
            </b-row>
        </b-col>  
        </b-row>
      <br />
      <b-row class="mb-2">
      <b-col xl="6">
          <b-row class="d-flex align-items-center">
            <b-col cols="12" xl="4">
              <b-button
                variant="primary"
                class="px-2 required-btnAny align-content-center"
                >{{ $t("REQUIREDBUTTON") }}</b-button
              >
              &nbsp;&nbsp;
              <label class="highLightHeaderLabel">{{ $t("INDUSTRY") }}</label>
            </b-col>
            <!-- <b-col cols="10" xl="6">
              <b-form-input
                v-model="$v.industryName.$model"
                disabled
                class="form-control"
                value="小売業"
              ></b-form-input>
              <show-errors :control="$v.industryName" :field="$t('INDUSTRY')" />
            </b-col> -->
          </b-row>
        </b-col>
      </b-row>
       <div class="border border-dark pl-4 pr-4 mt-2">
       <b-row>
              <SubMenuTree
                :treeTitle="''"
                @update-multiselect-industry-item="updateMultipleIndustryItem"
                :industry="true"
                :data="treeData"
                :tree-options="treeOptions"
              />
            </b-row>
      </div>
      <br />
      <b-button
        type="submit"
        variant="primary"
        class="px-4 align-content-center"
        >{{ buttonSelect }}
        <i v-if="isBusy" :disabled="isBusy" class="fa fa-spinner fa-spin"
      /></b-button>
      <br /><br />
    </b-form>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import SubMenuTree from "@/components/SubMenuTreeCheckboxIndustry";
import { ApiServicesFactory } from "@/shared/apiServiceFactory";
const BasicInfoApis = ApiServicesFactory.get("basic");
import { required } from "vuelidate/lib/validators";
import ShowErrors from "@/components/ShowErrors";
import { endPoint } from "@/shared/endPoint";

export default {
  name: "JudgementSettingYear",
  components: { SubMenuTree, ShowErrors },
  data() {
    return {
      account: "",
      itemCode: "",
      itemName: "",
      industryName: "",
      monthlyFee: "",
      upperLimit: "",
      lowerLimit: "",
      costFeeUpperLimit: "",
      selectedYear: "",
      options: [],
      CompanySizeoptions: [
        { item: "1", name: "小規模" },
        { item: "2", name: "中規模" },
        { item: "3", name: "大規模" },
      ],
      selectedCompanySize: "",
      treeOptions: {
        checkbox: true,
      },
      allTreeData: [],
      treeData: [],
      allIndustryTreeData: [],
      industryNo: "",
      kbn2Data: [],
      isBusy: "",
      accountCd: "",
      buttonSelect: this.$t("SAVE"),
      currentSelectedIndustryItem : "",
      checkedList: []
    };
  },
  validations: {
    industryName: { required },
    selectedCompanySize: { required },
  },
  computed: {
    ...mapGetters({
      masterData: "getExpenseYMData",
      selected_industry_item: "getSelectedIndustryItem",
    }),
  },
  mounted() {
    this.getIndustryTreeData();
    this.setYearOptions();
  },
  methods: {
     /**
     * @description function hide to pop-up modal
     * @name cancel
     */    
    cancel() {
      this.$refs["tree-modal1"].hide();
    },
     /**
     * @description function to set the yearoptions 
     * @name setYearOptions
     */    
    setYearOptions() {
      let startyear = parseInt(new Date().getFullYear()) - 10;
      let endYear = parseInt(new Date().getFullYear()) + 10;
      for (let index = startyear; index <= endYear; index++) {
        this.options.push({ item: index, name: index + this.$t("YEAR") });
      }
    },
    /**
     * @description function to convert number to currency format
     * @name filterByCurrency
     */     
    filterByCurrency(value) {
      try {
        return parseInt(value)
          .toString()
          .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1,");
      } catch {
        return 0;
      }
    },
    /**
     * @description function to convert currency to number
     * @name convertCurrency
     */      
    convertCurrency(value) {
      try {
        return String(value).replace(/,/g, "");
      } catch {
        return 0;
      }
    },
    /**
     * @description function to fetch the store data and map the data to corresponding fields
     * @name getStoreData
     */    
    getStoreData() {
      this.account = this.masterData.AccountCode;
      this.itemCode = this.masterData.ExpenseCode;
      this.itemName = this.masterData.ExpenseName;
      this.accountCd = this.masterData.AccountCd;
      if (this.masterData.YearData != null) {
        this.selectedYear = this.masterData.YearData.item.set_year;
        this.selectedCompanySize = this.masterData.YearData.item.company_size;
        this.monthlyFee = this.masterData.YearData.item.standard_monthly_fee;
        this.upperLimit = this.masterData.YearData.item.reduction_upper_limit;
        this.lowerLimit = this.masterData.YearData.item.reduction_lower_limit;
        this.costFeeUpperLimit = this.masterData.YearData.item.industry_average;
        this.allIndustryTreeData.forEach((item1) => {
          if (item1.industry_kbn == 2) {
            this.kbn2Data.push(item1);
          }
        });
        let IndustryItem = this._.find(this.kbn2Data, {
          industry_nm: this.masterData.YearData.item.industry,
        });
        this.industryNo = IndustryItem.industry_no;
        this.industryName = this.masterData.YearData.item.industry;
        this.buttonSelect = this.$t("UPDATE");
      } else {
        this.selectedYear = new Date().getFullYear();
        this.selectedCompanySize = 1;
        this.buttonSelect = this.$t("SAVE");
      }
    },
    /**
     * @description function to show the industry popup-modal
     * @name selectIndustry
     */            
    selectIndustry() {
      this.currentSelectedIndustryItem = "";
      this.$refs["tree-modal1"].show();
    },
    /**
     * @description function to fetch the industryTreeData form m_industry tabel
     * @name getIndustryTreeData
     */     
    async getIndustryTreeData() {
      const { data } = await BasicInfoApis.postAPI(
        endPoint.MASTERREADDATAMINDUSTRY,
        {
          fieldlist: ["*"],
          conditions: [
            {
              key: "industry_kbn",
              value: ["01", "02"],
            },
          ],
        }
      );
      if (data.success) {
        let responseData = data.data;
        this.allIndustryTreeData = responseData;
        let mapData = [];
        let sortData = this._.sortBy(responseData, "industry_big_cd");
        let groupedData = this._.groupBy(sortData, "industry_big_cd");
        Object.keys(groupedData).forEach((key) => {
          let childrenArray = [];
          let ParentData = [];
          groupedData[key].forEach((item) => {
            if (item.industry_kbn == 1) {
              let parentNode = item.industry_nm;
              ParentData.push(parentNode);
            } else {
              let bindChildData = {
                text: item.industry_nm,
              };
              childrenArray.push(bindChildData);
            }
          });
          let name = ":" + ParentData[0];
          if (key == "A") {
            let bindTreeData = {
              text: key + name,
              state: { expanded: true },
              children: childrenArray,
            };
            mapData.push(bindTreeData);
          } else {
            let bindTreeData = {
              text: key + name,
              state: { expanded: false },
              children: childrenArray,
            };
            mapData.push(bindTreeData);
          }
        });
        this.$store.dispatch("updateIndustryTree", mapData);
        this.getStoreData();
      }
    },
   /**
     * @description function to update the selected industry(form industry popup-modal) name and no to the store
     * @name updateIndustryItem
     */    
    updateIndustryItem(item) {
      this.allIndustryTreeData.forEach((item1) => {
        if (item1.industry_kbn == 2) {
          this.kbn2Data.push(item1);
        }
      });

      let treeItem = this._.find(this.kbn2Data, { industry_nm: item });
      this.$store.dispatch("updateselectedIndustryItem", {
        industry_nm: treeItem.industry_nm,
        industry_no: treeItem.industry_no,
      });
      this.currentSelectedIndustryItem = treeItem.industry_nm;
      this.getIndustryName();
    },
   /**
     * @description function to update the multiselected industry name and no to the store
     * @name updateMultipleIndustryItem
     */    
    updateMultipleIndustryItem(item) {
      this.kbn2Data=[];
      this.checkedList=[];
      this.allIndustryTreeData.forEach((item1) => {
        if (item1.industry_kbn == 2) {
          this.kbn2Data.push(item1);
        }
      });
      for(var i=0;i<this.kbn2Data.length;i++)
      {
        item.indexOf(this.kbn2Data[i].industry_nm)!==-1 ?
        this.checkedList.push(this.kbn2Data[i].industry_nm) : "";
      }
      console.log(this.checkedList.length);
    },
    /**
     * @description function to fetch the industy name and no from store and closing the popup-modal
     * @name getIndustryName
     */    
    getIndustryName() {
      //console.log("DATA", this.selected_industry_item);
      this.currentSelectedIndustryItem = this.selected_industry_item.industry_nm;
      this.industryName = this.selected_industry_item.industry_nm;
      this.industryNo = this.selected_industry_item.industry_no;

      //this.$refs["tree-modal1"].hide();
    },
    /**
     * @description function to validate the form data on submiting checking either to create or update
     * @name submit
     */     
    submit() {
      this.$v.selectedCompanySize.$touch();
      this.$v.industryName.$touch();
      if (
        !this.$v.selectedCompanySize.$invalid
      ) {
        if (this.masterData.YearData != null) {
          this.updateData();
        } else {
          for(var j=0;j<this.checkedList.length;j++)
          {
          let treeItem = this._.find(this.kbn2Data, { industry_nm: this.checkedList[j]});
          this.$store.dispatch("updateselectedIndustryItem", {
          industry_nm: treeItem.industry_nm,
          industry_no: treeItem.industry_no,
          });
         this.currentSelectedIndustryItem = treeItem.industry_nm;
         console.log("this.currentSelectedIndustryItem "+this.currentSelectedIndustryItem);
         this.getIndustryName();
         this.createData();
        }
        }
      }
    },
    /**
     * @description function to update the data to database
     * @name updateData
     */    
    async updateData() {
      this.isBusy = true;
      const { data } = await BasicInfoApis.postAPI(
        endPoint.MASTERUPDATEMEXPENSEITEMSETY,
        {
          data: [
            {
              set_year: this.selectedYear,
              industry_no: this.industryNo,
              company_size: this.selectedCompanySize,
              accounts_name_cd: this.accountCd,
              expense_item_cd: this.itemCode,
              standard_annual_savings: this.monthlyFee
                ? this.convertCurrency(this.monthlyFee)
                : "",
              reduction_range_up: this.upperLimit,
              reduction_range_low: this.lowerLimit,
              cost_industry_average: this.costFeeUpperLimit
                ? this.convertCurrency(this.costFeeUpperLimit)
                : "",
              del_flg: false,
              update_id: localStorage.getItem("client_account_no"),
            },
          ],
          conditions: [
            {
              key: "expense_item_set_y_no",
              value: this.masterData.YearData.item.set_y_no,
            },
          ],
        }
      );
      if (data.success) {
        this.isBusy = false;
        this.$router.push({ name: "expense_item_add_edit" });
      } else {
        this.isBusy = false;
      }
    },
    /**
     * @description function to create or insert the data  (register to database)
     * @name createData
     */      
    async createData() {
      this.isBusy = true;
      const { data } = await BasicInfoApis.postAPI(
        endPoint.MASTERCREATEMEXPENSEITEMSETY,
        [
          {
            set_year: this.selectedYear,
            industry_no: this.industryNo,
            company_size: this.selectedCompanySize,
            accounts_name_cd: this.accountCd,
            expense_item_cd: this.itemCode,
            standard_annual_savings: this.monthlyFee,
            reduction_range_up: this.upperLimit,
            reduction_range_low: this.lowerLimit,
            cost_industry_average: this.costFeeUpperLimit,
            del_flg: false,
            update_id: localStorage.getItem("client_account_no"),
            create_id: localStorage.getItem("client_account_no"),
          },
        ]
      );
      if (data.success) {
        this.isBusy = false;
        this.$router.push({ name: "expense_item_add_edit" });
      } else {
        this.isBusy = false;
      }
    },
  },
};
</script>

<style>
.required-btnAny {
  padding: 2px 5px 2px 5px !important;
  min-width: 10px !important;
  font-size: 11px;
  border-radius: 0px;
  cursor: default !important;
}
.highLightHeaderLabel {
  font-weight: 700 !important;
  font-size: 0.8rem !important;
  padding-top: 9px;
  font-size: initial;
}
#tree-modal1 .card-body {
  max-height: 500px;
  overflow: auto;
}
</style>