<template>
  <div class="min-tree-height">
    <div class="font-weight-bold">{{ treeTitle }}</div>
    <template v-if="treeData.length == 0">
      <div class="text-center errorColor">
        {{ $t("GENRALDATAERRORMESSAGE") }}
      </div>
    </template>
    <template v-else>
      <tree
        ref="tree"
        :data="treeData"
        :options="treeOptions"
        @node:checked="getCheckedItem"
        @node:unchecked="getCheckedItem"
      >
        <span slot-scope="{ node }" class="tree-text">
          <template v-if="!node.hasChildren()">
            <u
              :class="
                selected_expense_item.expense_item_nm == node.text
                  ? 'selected-tree-text'
                  : ''
              "
              class="tree-text"
            >
              <span> {{ node.text }} </span>
            </u>
          </template>

          <template v-else>
            <u>{{ node.text }}</u>
          </template>
        </span>
      </tree>
    </template>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  name: "SubMenuTree",
  components: {},
  inheritAttrs: false,
  props: {
    treeOptions: {
      type: [Array, Object, String, Function],
      default: () => [],
    },
    treeTitle: {
      type: String,
      default: () => [],
    },
    industry: {
      type: Boolean,
      default: false,
    },
  },
  data: () => {
    return {
      ids: [],
      checkedItems: [],
    };
  },
  computed: {
    eventsList() {
      return this.events.concat().reverse();
    },
    ...mapGetters({
      industryTree: "getIndustryTree",
      treeData: "getIndustryTree",
      selected_expense_item: "getSelectedExpenseItem",
      checkedItemsExpCodeArray: "getCheckedExpItemCodeArray",
    }),
  },
  mounted() {
  },
  methods: {
    getCheckedItem() {
      let checkedItems = this.$refs.tree
        .findAll({ state: { checked: true } })
        .map((item) => {
          return item.text;
        });  
      this.$emit("update-multiselect-industry-item", checkedItems);
    },
  },
};
</script>
<style scoped lang="scss">
$primary: #062854 !default;

.selected-tree-text {
  color: blue;
}
.tree-text,
.link-text {
  font-size: 0.7rem;
  font-weight: 800;
  color: $primary;
}
</style>
<style lang="scss" >
.tree-content {
  padding-top: 0px !important;
  padding-bottom: 0px !important;
}

.tree-checkbox.checked,
.tree-checkbox.indeterminate {
  background-color: #fff !important;
  border-color: #000 !important;
}

.tree-checkbox {
  border-color: #000 !important;
  height: 23px !important;
  width: 23px !important;
}

.tree-checkbox.checked:after {
  border: 1.5px solid #000 !important;
  border-left: 0 !important;
  border-top: 0 !important;
  left: 8px !important;
  top: 2px !important;
  height: 12px !important;
  width: 5px !important;
}
.tree-children > li {
  display: inline-flex !important;
  float: left;
}
</style>
